.profile-container {
  padding: 20px;
}

.profile-header {
  display: flex;
  align-items: center;
  gap: 20px;
}

.profile-picture {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
}

.profile-info h2 {
  margin: 0;
}

.profile-info p {
  margin: 0;
  color: #555;
}

.profile-container{
  font-family: "Aptos";
}